import { FC, ReactNode, useMemo } from 'react';
import classNames from 'classnames';

// Styles
import './UtilityBar.scss';

// Types
import { TestMetadata } from 'types';

export interface UtilityBarProps {
  /** classname */
  className?: string;
  /** section on the left of the UtilityBar */
  leftSection?: ReactNode;
  /** section in the center of the UtilityBar */
  centerSection?: ReactNode;
  /** section on the left of the UtilityBar */
  rightSection?: ReactNode;
  /** background color for UtilityBar - transparent (default), light, dark */
  background?: 'light' | 'dark' | 'transparent';
  // Test IDs for component sections
  testMetadata?: TestMetadata;
}

const UtilityBar: FC<UtilityBarProps> = ({
  className,
  leftSection,
  centerSection,
  rightSection,
  testMetadata,
  background = 'transparent',
}) => {
  const utilityBarClassNames = useMemo(
    () =>
      classNames(
        'sb-utility-bar',
        {
          'sb-utility-bar-transparent': background === 'transparent',
          'sb-utility-bar-dark': background === 'dark',
          'sb-utility-bar-light': background === 'light',
        },
        className,
      ),
    [background, className],
  );
  return (
    <div className={utilityBarClassNames} {...testMetadata}>
      <div className="sb-utility-bar__left-region">{leftSection}</div>
      <div className="sb-utility-bar__center-region">{centerSection}</div>
      <div className="sb-utility-bar__right-region">{rightSection}</div>
    </div>
  );
};

export default UtilityBar;
